@import './variables.scss';

.socials {
  width: 100%;
  display: flex;
  padding: 1rem 0;
  transition: 0.4s ease all;
  cursor: pointer;
  // @include breakpoint(mobile-landscape) {
  //   width: 100%;
  //   margin-right: 1rem;
  //   display: flex;
  //   margin-top: 2rem;
  // }

  &__icon {
    font-size: 2.5rem;
    padding-right: 0 2rem;
    &:hover {
      color: grey;
    }
    @include breakpoint(mobile-landscape) {
      margin: 0 2rem 0 0;
    }
  }
}
