@import './variables';
.newsletter-container {
  display: flex;
  justify-content: center;
  background-color: teal;
  padding: 2rem;
}
.newsletter {
  position: relative;
  padding: 3rem;
  display: flex;
  align-items: center;
  color: white;
  background-color: black;
  @include breakpoint(mobile-landscape) {
    padding: 2rem;
    width: 100%;
    flex-direction: column;
  }

  &__con {
    margin-right: 3rem;
    padding: 1rem 3rem;

    @include breakpoint(mobile-landscape) {
      margin-right: 0;
      padding: 0;
    }
  }

  &__header {
    font-size: 3rem;
    text-align: center;
    margin-bottom: 1rem;
    line-height: 1;
  }
  &__title {
    text-align: center;
    font-size: 1.8rem;
    margin-bottom: 1rem;
    font-weight: 400;
    line-height: 1;
    color: teal;
  }

  &__text {
    font-size: 1.4rem;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: 200;
  }
  &__input-con {
    display: flex;
    width: 100%;
    height: 4rem;
    @include breakpoint(mobile-landscape) {
      flex-direction: column;
      height: fit-content;
    }
  }

  &__input {
    width: 100%;
    padding: 1rem;
    margin-right: 2rem;
    background-color: whitesmoke;
    font-size: 1.6rem;
    @include breakpoint(mobile-landscape) {
      margin-bottom: 1rem;
    }
  }

  &__btn {
    width: fit-content;
    background-color: black;
    border: solid white 0.1rem;
    color: white;
    text-align: center;
    font-size: 1.5rem;
    padding: 0 1rem;
    cursor: pointer;
    transition: ease 0.3s;
    &:hover {
      background-color: grey;
    }
    @include breakpoint(mobile-landscape) {
      width: 100%;
      padding: 1rem;
    }
  }

  &__small {
    margin-top: 2rem;
    font-size: 1.2rem;
    text-align: center;
    color: grey;
  }
}
