@import './variables.scss';

.bannerOne {
  background-color: black;
  color: orangered;
  padding: 0em 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-family: 'Tourney', cursive;
  overflow: hidden;

  @include breakpoint(tablet-landscape) {
    padding: 1rem 2rem;
    font-size: 1rem;
  }

  &__btn {
    color: orangered;
    border: solid orangered 0.01rem;
    border-radius: 0.5rem;
    text-decoration: none;
    padding: 0.5rem;
    font-size: 2rem;
    transition: ease-in-out 0.3s;
    margin-right: 3rem;
    &:hover {
      background-color: orangered;
      color: black;
    }
  }
}

.bannerOne h1 {
  font-weight: 700;
}

.bannerThree {
  display: flex;
  padding: 0 18rem;

  @include breakpoint(tablet-landscape) {
    padding: 0 2rem;
  }
  @include breakpoint(mobile-landscape) {
    flex-direction: column;
  }

  &__container {
    padding: 3rem;
    margin-right: 5rem;
    width: 70rem;
    border-radius: 0.2rem;
    background-color: black;
    @include breakpoint(tablet-portrait) {
      margin-right: 2rem;
    }
    @include breakpoint(mobile-landscape) {
      width: 100%;
      margin-bottom: 1rem;
    }
  }

  &__text {
    font-size: 4.5rem;
    font-family: 'Tourney', cursive;
    color: white;
    font-weight: 300;
  }

  &__link {
    padding: 1rem;
    font-size: 1.4rem;
    background-color: white;
    color: black;
    font-weight: 500;
    cursor: pointer;
    text-decoration: none;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;

    @include breakpoint(tablet-landscape) {
      // width: 15rem;
    }
  }

  &__col {
    display: grid;
    width: 100%;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }

  &__rows {
    border-radius: 0.5rem;
    overflow: hidden;
  }
}

.span-2 {
  grid-column: span 2;
  // height: 15rem;
}

.span-2-row {
  grid-row: span 2;
}
